import React from 'react'
import {Link} from 'react-router'
export default class Order extends React.Component{
    constructor(props) {
        super(props)
        this.state={

        }
    }
    handleClick(orderGid) {
        location.hash="orderDetail/"+orderGid;
    }
    render() {
            return (
                <div className="orderBox">
                {
                  this.props.orderList.map((item,i)=>
                  <Link to={`/MyorderDetail/${item.orderGid}`}>
                        <div className="item1">
                            <div className="itemTitle1">
                                <div>{item.stateName==1 ? "" : ""}</div>
                                <p>{item.createTime}</p>
                            </div>
                            <div className="itemContent1">
                                <div className="money">
                                    <p className="first">{item.loadMoney}</p>
                                    <p className="secound">()</p>
                                </div>
                                <div className="type">
                                    <p className="first">{item.type==1? "" : ""}</p>
                                    <p className="secound"></p>
                                </div>
                                <div className="number">
                                    <p className="first">{item.per}</p>
                                    <p className="secound"></p>
                                </div>
                            </div>
                        </div>
                  </Link>
                    )
                }
              </div>
        )
    }
}


