import React from "react"
export default class Cmount extends React.Component{
    constructor(props){
        super(props);
        this.state={
            enable:false
        }
    }
    handleClick(){
        this.setState({
            enable:!this.state.enable
        })
    }
    componentWillMount() {
        console.log("");
        this.timer=setInterval(()=>console.log(""),500);
    }
    render(){
        console.log("");
        return (
            <div className="lifeCycle">
                <h3>React</h3>
                <ul>
                    <p></p>
                    <li>1componentWillMount render render</li>
                    <li>2componentDidMound  renderstaterender</li>
                    <li>3componentWillUnmount dom</li>
                    <p></p>
                    <li>1componentWillUpdate state</li>
                    <li>2componentDidUpdate   </li>
                </ul>
                <div>
                    <input type="text" value={this.state.enable ? "disable" : "enable"} className={this.state.enable ? "bgColor" : ""} disabled={this.state.enable} />
                    <button onClick={this.handleClick.bind(this)}>state</button>
                </div>
            </div>
        )
    }
    componentDidMount(){
        console.log("")
    }
    componentWillUpdate(){
        console.log("")
    }
    componentDidUpdate(){
        console.log("")
    }
    componentWillUnmount(){
        console.log("");
        alert("");
        clearInterval(this.timer);
    }
}


